"use client";

import * as React from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { ShoppingBag, Menu, X, User, LogOut, Settings } from "lucide-react";
import { motion, AnimatePresence } from "framer-motion";
import { useSession, signOut } from "next-auth/react";
import { LoginModal } from "@/components/auth/LoginModal";
import { cn } from "@/lib/utils";
import { useCart } from "@/components/cart/CartProvider";

export function Navigation() {
    const [isMobileMenuOpen, setIsMobileMenuOpen] = React.useState(false);
    const [isLoginOpen, setIsLoginOpen] = React.useState(false);
    const [isUserMenuOpen, setIsUserMenuOpen] = React.useState(false);

    const pathname = usePathname();
    const { cartCount, setIsCartOpen } = useCart();
    const { data: session } = useSession();
    const [scrolled, setScrolled] = React.useState(false);

    React.useEffect(() => {
        const handleScroll = () => {
            setScrolled(window.scrollY > 20);
        };
        window.addEventListener("scroll", handleScroll);
        return () => window.removeEventListener("scroll", handleScroll);
    }, []);

    React.useEffect(() => {
        setIsMobileMenuOpen(false);
        setIsUserMenuOpen(false);
    }, [pathname]);

    const navLinks = [
        { name: "Home", href: "/" },
        { name: "Shop", href: "/products" },
        { name: "About", href: "/about" },
    ];

    const isActive = (path: string) => {
        if (path === "/" && pathname !== "/") return false;
        return pathname.startsWith(path);
    };

    const handleSignOut = () => {
        signOut();
        setIsUserMenuOpen(false);
    };

    return (
        <>
            <header
                className={cn(
                    "fixed top-0 left-0 right-0 z-40 transition-all duration-300 border-b border-transparent",
                    scrolled || isMobileMenuOpen
                        ? "bg-white/80 backdrop-blur-md border-border/50 shadow-sm"
                        : "bg-transparent"
                )}
            >
                <div className="container-custom mx-auto">
                    <div className="flex items-center justify-between h-20">
                        {/* Logo */}
                        <Link
                            href="/"
                            className="flex items-center gap-2 group"
                        >
                            <div className="w-8 h-8 rounded-lg bg-primary flex items-center justify-center text-white font-bold text-xl shadow-lg group-hover:bg-primary-dark transition-colors">
                                I
                            </div>
                            <span className="text-2xl font-display font-bold bg-clip-text text-transparent bg-gradient-to-r from-primary to-secondary">
                                intru.in
                            </span>
                        </Link>

                        {/* Desktop Navigation */}
                        <nav className="hidden md:flex items-center gap-8">
                            {navLinks.map((link) => (
                                <Link
                                    key={link.name}
                                    href={link.href}
                                    className={cn(
                                        "text-sm font-medium transition-colors hover:text-primary relative group py-2",
                                        isActive(link.href)
                                            ? "text-primary"
                                            : "text-text-secondary"
                                    )}
                                >
                                    {link.name}
                                    <span
                                        className={cn(
                                            "absolute bottom-0 left-0 w-full h-0.5 bg-primary transform origin-left transition-transform duration-300",
                                            isActive(link.href) ? "scale-x-100" : "scale-x-0 group-hover:scale-x-100"
                                        )}
                                    />
                                </Link>
                            ))}
                        </nav>

                        {/* Actions */}
                        <div className="flex items-center gap-4">
                            <button
                                onClick={() => setIsCartOpen(true)}
                                className="relative p-2 text-text-secondary hover:text-primary transition-colors hover:bg-surface-elevated rounded-full"
                                aria-label="Open Cart"
                            >
                                <ShoppingBag className="w-5 h-5" />
                                {cartCount > 0 && (
                                    <span className="absolute top-0 right-0 w-5 h-5 bg-primary text-white text-[10px] font-bold flex items-center justify-center rounded-full ring-2 ring-white transform translate-x-1/4 -translate-y-1/4 animate-scale-in">
                                        {cartCount}
                                    </span>
                                )}
                            </button>

                            {/* User Menu */}
                            <div className="relative">
                                {session ? (
                                    <button
                                        onClick={() => setIsUserMenuOpen(!isUserMenuOpen)}
                                        className="flex items-center justify-center w-9 h-9 rounded-full bg-primary/10 text-primary hover:bg-primary/20 transition-colors"
                                    >
                                        <span className="font-bold">{session.user?.name?.[0] || "U"}</span>
                                    </button>
                                ) : (
                                    <button
                                        onClick={() => setIsLoginOpen(true)}
                                        className="p-2 text-text-secondary hover:text-primary transition-colors hover:bg-surface-elevated rounded-full"
                                        aria-label="Sign In"
                                    >
                                        <User className="w-5 h-5" />
                                    </button>
                                )}

                                {/* Dropdown */}
                                <AnimatePresence>
                                    {isUserMenuOpen && (
                                        <motion.div
                                            initial={{ opacity: 0, scale: 0.95, y: 10 }}
                                            animate={{ opacity: 1, scale: 1, y: 0 }}
                                            exit={{ opacity: 0, scale: 0.95, y: 10 }}
                                            className="absolute right-0 top-full mt-2 w-48 bg-surface rounded-xl shadow-xl border border-border p-2 z-50 overflow-hidden"
                                        >
                                            <div className="px-3 py-2 border-b border-border/50 mb-1">
                                                <p className="font-medium text-sm truncate">{session?.user?.name}</p>
                                                <p className="text-xs text-text-tertiary truncate">{session?.user?.email}</p>
                                            </div>
                                            {(session?.user as any)?.role === "admin" && (
                                                <Link href="/admin/products" className="flex items-center w-full px-3 py-2 text-sm text-text-secondary hover:text-primary hover:bg-surface-elevated rounded-lg">
                                                    <Settings className="w-4 h-4 mr-2" /> Admin Dashboard
                                                </Link>
                                            )}
                                            <button
                                                onClick={handleSignOut}
                                                className="flex items-center w-full px-3 py-2 text-sm text-error hover:bg-error/10 rounded-lg transition-colors"
                                            >
                                                <LogOut className="w-4 h-4 mr-2" /> Sign Out
                                            </button>
                                        </motion.div>
                                    )}
                                </AnimatePresence>
                            </div>

                            {/* Mobile Menu Button */}
                            <button
                                onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
                                className="md:hidden p-2 text-text-secondary hover:text-primary transition-colors"
                            >
                                {isMobileMenuOpen ? (
                                    <X className="w-6 h-6" />
                                ) : (
                                    <Menu className="w-6 h-6" />
                                )}
                            </button>
                        </div>
                    </div>
                </div>

                {/* Mobile Navigation */}
                <AnimatePresence>
                    {isMobileMenuOpen && (
                        <motion.div
                            initial={{ opacity: 0, height: 0 }}
                            animate={{ opacity: 1, height: "auto" }}
                            exit={{ opacity: 0, height: 0 }}
                            className="md:hidden border-t border-border bg-surface overflow-hidden"
                        >
                            <div className="container-custom py-6 space-y-4">
                                {navLinks.map((link) => (
                                    <Link
                                        key={link.name}
                                        href={link.href}
                                        className={cn(
                                            "block text-lg font-medium px-4 py-3 rounded-lg transition-colors",
                                            isActive(link.href)
                                                ? "bg-primary/10 text-primary"
                                                : "text-text-secondary hover:bg-surface-elevated"
                                        )}
                                    >
                                        {link.name}
                                    </Link>
                                ))}
                            </div>
                        </motion.div>
                    )}
                </AnimatePresence>
            </header>

            {/* Login Modal */}
            <LoginModal isOpen={isLoginOpen} onClose={() => setIsLoginOpen(false)} />
        </>
    );
}
