"use client";

import React, { useState } from "react";
import { signIn } from "next-auth/react";
import { Mail, Loader2, AlertCircle } from "lucide-react";
import { Button } from "@/components/ui/Button";
import { Input } from "@/components/ui/Input";
import { Modal } from "@/components/ui/Modal";

interface LoginModalProps {
    isOpen: boolean;
    onClose: () => void;
}

export function LoginModal({ isOpen, onClose }: LoginModalProps) {
    const [isLoading, setIsLoading] = useState(false);
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [error, setError] = useState("");
    const [mode, setMode] = useState<"login" | "register">("login");

    const handleGoogleLogin = async () => {
        setIsLoading(true);
        try {
            await signIn("google", { callbackUrl: "/" });
        } catch (err) {
            setError("Failed to sign in with Google");
        } finally {
            setIsLoading(false);
        }
    };

    const handleEmailLogin = async (e: React.FormEvent) => {
        e.preventDefault();
        setIsLoading(true);
        setError("");

        if (mode === "register") {
            // In a real app, calls to registration API
            // For now, simulate error or switch to mock login
            await new Promise(r => setTimeout(r, 1000));
            setMode("login"); // Just switch to login for demo
            setError("Registration successful! Please log in.");
            setIsLoading(false);
            return;
        }

        try {
            const result = await signIn("credentials", {
                redirect: false,
                email,
                password,
            });

            if (result?.error) {
                setError("Invalid email or password");
            } else {
                onClose();
            }
        } catch (err) {
            setError("Something went wrong");
        } finally {
            setIsLoading(false);
        }
    };

    return (
        <Modal
            isOpen={isOpen}
            onClose={onClose}
            title={mode === "login" ? "Welcome Back" : "Create Account"}
            description={
                mode === "login"
                    ? "Sign in to access your account and orders."
                    : "Join us to shop premium products and track orders."
            }
            className="sm:max-w-[400px]"
        >
            <div className="space-y-6">
                {/* Google One Tap / Button */}
                <Button
                    variant="outline"
                    className="w-full relative"
                    onClick={handleGoogleLogin}
                    isLoading={isLoading}
                >
                    {/* Fallback for Google Icon since it's not in standard lucide */}
                    <svg className="mr-2 h-4 w-4" aria-hidden="true" focusable="false" data-prefix="fab" data-icon="google" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 488 512"><path fill="currentColor" d="M488 261.8C488 403.3 391.1 504 248 504 110.8 504 0 393.2 0 256S110.8 8 248 8c66.8 0 123 24.5 166.3 64.9l-67.5 64.9C258.5 52.6 94.3 116.6 94.3 256c0 86.5 69.1 156.6 153.7 156.6 98.2 0 135-70.4 140.8-106.9H248v-85.3h236.1c2.3 12.7 3.9 24.9 3.9 41.4z"></path></svg>
                    Continue with Google
                </Button>

                <div className="relative">
                    <div className="absolute inset-0 flex items-center">
                        <span className="w-full border-t border-border" />
                    </div>
                    <div className="relative flex justify-center text-xs uppercase">
                        <span className="bg-surface px-2 text-text-tertiary">Or continue with</span>
                    </div>
                </div>

                <form onSubmit={handleEmailLogin} className="space-y-4">
                    <Input
                        type="email"
                        placeholder="name@example.com"
                        label="Email"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                        required
                        leftIcon={<Mail className="h-4 w-4" />}
                    />
                    <Input
                        type="password"
                        placeholder="••••••••"
                        label="Password"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        required
                    />

                    {error && (
                        <div className="bg-error/10 text-error text-sm p-3 rounded-lg flex items-center gap-2">
                            <AlertCircle className="w-4 h-4" />
                            {error}
                        </div>
                    )}

                    <Button type="submit" className="w-full" isLoading={isLoading}>
                        {mode === "login" ? "Sign In" : "Create Account"}
                    </Button>
                </form>

                <div className="text-center text-sm text-text-secondary">
                    {mode === "login" ? "Don't have an account? " : "Already have an account? "}
                    <button
                        onClick={() => {
                            setMode(mode === "login" ? "register" : "login");
                            setError("");
                        }}
                        className="text-primary hover:underline font-medium"
                    >
                        {mode === "login" ? "Sign up" : "Log in"}
                    </button>
                </div>
            </div>
        </Modal>
    );
}
