import * as React from "react";
import { X } from "lucide-react";
import { motion, AnimatePresence } from "framer-motion";
import { cn } from "@/lib/utils";
import { Button } from "@/components/ui/Button";

interface ModalProps {
    isOpen: boolean;
    onClose: () => void;
    title?: string;
    description?: string;
    children: React.ReactNode;
    footer?: React.ReactNode;
    size?: "sm" | "md" | "lg" | "xl" | "full";
    className?: string;
}

export function Modal({
    isOpen,
    onClose,
    title,
    description,
    children,
    footer,
    size = "md",
    className,
}: ModalProps) {
    // Lock body scroll when modal is open
    React.useEffect(() => {
        if (isOpen) {
            document.body.style.overflow = "hidden";
        } else {
            document.body.style.overflow = "unset";
        }
        return () => {
            document.body.style.overflow = "unset";
        };
    }, [isOpen]);

    const sizes = {
        sm: "max-w-sm",
        md: "max-w-lg",
        lg: "max-w-2xl",
        xl: "max-w-4xl",
        full: "max-w-[95vw] h-[95vh]",
    };

    return (
        <AnimatePresence>
            {isOpen && (
                <>
                    {/* Backdrop */}
                    <motion.div
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        exit={{ opacity: 0 }}
                        onClick={onClose}
                        className="fixed inset-0 z-50 bg-black/60 backdrop-blur-sm transition-opacity"
                        aria-hidden="true"
                    />

                    {/* Modal Container */}
                    <div className="fixed inset-0 z-50 flex items-center justify-center p-4 sm:p-6 overflow-y-auto pointer-events-none">
                        <motion.div
                            initial={{ opacity: 0, scale: 0.95, y: 20 }}
                            animate={{ opacity: 1, scale: 1, y: 0 }}
                            exit={{ opacity: 0, scale: 0.95, y: 20 }}
                            transition={{ type: "spring", duration: 0.3, bounce: 0 }}
                            className={cn(
                                "relative flex flex-col w-full bg-surface rounded-xl shadow-2xl pointer-events-auto border border-white/10 overflow-hidden",
                                sizes[size],
                                className
                            )}
                            role="dialog"
                            aria-modal="true"
                        >
                            {/* Close Button */}
                            <div className="absolute right-4 top-4 z-10">
                                <button
                                    onClick={onClose}
                                    className="rounded-full p-2 text-text-tertiary hover:bg-surface-elevated hover:text-text-primary transition-colors focus:outline-none focus:ring-2 focus:ring-primary"
                                >
                                    <X className="h-5 w-5" />
                                    <span className="sr-only">Close</span>
                                </button>
                            </div>

                            {/* Header */}
                            {(title || description) && (
                                <div className="px-6 pt-6 pb-4 border-b border-border/50">
                                    {title && (
                                        <h2 className="text-xl font-semibold leading-none tracking-tight">
                                            {title}
                                        </h2>
                                    )}
                                    {description && (
                                        <p className="mt-2 text-sm text-text-secondary">
                                            {description}
                                        </p>
                                    )}
                                </div>
                            )}

                            {/* Content */}
                            <div className="flex-1 overflow-y-auto px-6 py-4">
                                {children}
                            </div>

                            {/* Footer */}
                            {footer && (
                                <div className="flex items-center justify-end gap-3 px-6 py-4 border-t border-border/50 bg-surface-elevated/30">
                                    {footer}
                                </div>
                            )}
                        </motion.div>
                    </div>
                </>
            )}
        </AnimatePresence>
    );
}
