"use client";

import React from "react";
import { cn } from "@/lib/utils";

interface MarqueeProps {
    className?: string;
    children: React.ReactNode;
    pauseOnHover?: boolean;
    direction?: "left" | "right";
    speed?: number;
}

export function Marquee({
    className,
    children,
    pauseOnHover = false,
    direction = "left",
    speed = 40
}: MarqueeProps) {
    return (
        <div className={cn("group flex overflow-hidden border-y border-border bg-black text-white py-3", className)}>
            <div
                className={cn(
                    "flex min-w-full shrink-0 items-center justify-around gap-4 whitespace-nowrap animate-scroll",
                    direction === "right" && "animate-scroll-reverse",
                    pauseOnHover && "group-hover:[animation-play-state:paused]"
                )}
                style={{ animationDuration: `${speed}s` }}
            >
                {children}
                {children}
                {children}
                {children}
            </div>
        </div>
    );
}
